export type edge = {
    id: number;
    v1: number;
    v2: number;
    weight: number;
    selected: boolean;
};

export type graph = {
    nVertices: number;
    nEdges: number;
    edges: edge[];
};

export function createAdjMatrix (g: graph){
    const matrix: number[][] = [];
    for (const link of g.edges){
        const v = link.v1;
        const w = link.v2;
        matrix[v][w] = link.weight;
        matrix[w][v] = link.weight;
    }
    

}

let pred = [];

export function checkValidity(g: graph, sel: number[], origin: number, sink: number){
    const elements = new Set();
    for (let i = 0; i < sel.length; i++){
        const index = sel[i];
        const v1 = g.edges[index].v1;
        const v2 = g.edges[index].v2;
        if (elements.has(v1)){
            elements.delete(v1);
        } else {
            elements.add(v1);
        }
        if (elements.has(v2)){
            elements.delete(v2);
        } else {
            elements.add(v2);
        }
    }
    const size = elements.size == 2;
    const disconnected = elements.size > 2;
    const nodes = elements.has(origin) && elements.has(sink);
    const empty = sel.length == 0;
    let ret;
    if (size && nodes) { // path is correct
        ret = 2;
    } else if (size){ // path doesnt have correct start and end node
        ret = 1;
    } else if (empty){ // nothing has been selected
        ret = 3;
    } else if (disconnected){ // path is not connected aka more than two end nodes
        ret = 0;
    } else { // cycle in path
        ret = 4;       
    }
    return ret;
    //return (elements.size == 2 && elements.has(origin) && elements.has(sink));
} 


// checks if roundtrip from origin to origin via waypoint is correct 
export function checkRoundtrip(g: graph, sel: number[], origin: number, waypoint: number){
    let countOrigin = 0;
    let countWP = 0;
    
    for (let i = 0; i < sel.length; i++){
        const index = sel[i];
        const v1 = g.edges[index].v1;
        const v2 = g.edges[index].v2;
        if (v1 == origin || v2 == origin){
            countOrigin++;
        } else if (v1 == waypoint || v2 == waypoint){
            countWP++;
        }
    }
    const ret = countOrigin == 2 && countWP == 2;

    return ret;
} 

// check if the roundtrip is valid 
export function checkSimpleMushRoundtrip(g: graph, sel: number[], origin: number){
    let countOrigin = 0;

    for (let i = 0; i < sel.length; i++){
        const index = sel[i];
        const v1 = g.edges[index].v1;
        const v2 = g.edges[index].v2;
        if (v1 == origin || v2 == origin){
            countOrigin++;
        } 
    }
    const ret = countOrigin == 2;

    return ret;
}

// get the amount of mushrooms collected from the set of selected edges
export function getMushCount(g: graph, sel: number[], mushrooms: number[]): number{
    const nodes = new Set();
    let count = 0;
    for (let i = 0; i < sel.length; i++){
        const index = sel[i];
        const v1 = g.edges[index].v1;
        const v2 = g.edges[index].v2;
        nodes.add(v1);
        nodes.add(v2);
    }
    for (let i  = 0; i < mushrooms.length; i++){
        if (nodes.has(i)){
            count += mushrooms[i];
        }
    }
    return count;
}


// gets the edge numbers of the edges involved in the result path
export function getResultEdges (g: graph, path: number[]): number[] {
    const resultEdges: number[] = [];

    for (let i = 0; i < path.length - 1; i++){
        const start = path[i];
        const end = path[i+1];
        for (let j = 0; j < g.edges.length; j++){
            if ((g.edges[j].v1 == start && g.edges[j].v2 == end) || (g.edges[j].v1 == end && g.edges[j].v2 == start)){
                resultEdges.push(g.edges[j].id)
                break;
            }
        }
    }
    return resultEdges;
}

// perform dijkstra on a graph to get shortest path from origin to sink (returns length of shortest path)
export function dijkstra (g: graph, origin: number, sink: number, ): number {
    const optimized: boolean[] = new Array(g.nVertices).fill(false);
    const matrix: number[][] = [];
    for (let i = 0; i < g.nVertices; i++){
        const temp = [];
        for(let j = 0; j < g.nVertices; j++){
            temp.push(0);
        }
        matrix.push(temp);
    }
    for (let i  = 0; i < g.nEdges; i++){
        const v = g.edges[i].v1;
        const w = g.edges[i].v2;
        matrix[v][w] = g.edges[i].weight;
        matrix[w][v] = g.edges[i].weight;
    }
    const distances: number[] = new Array(g.nVertices).fill(Number.POSITIVE_INFINITY);
    const predecessors: number[] = new Array(g.nVertices);
    distances[origin] = 0;
    while (optimized.filter(function(x: boolean): boolean{return !x}).length != 0) {
        let v: number;
        let min: number = Number.POSITIVE_INFINITY;
        let i = 0;
        for (const dist of distances) {
            if (dist <= min && !optimized[i]) {
                v = i;
                min = dist;
            }
            i++;
        }
        optimized[v] = true;
        for (let j  = 0; j < matrix[v].length; j++) {
            if (matrix[v][j] !== 0 && v !== j) {
                if (distances[v] + matrix[v][j] < distances[j]) {
                    distances[j] = distances[v] + matrix[v][j];
                    predecessors[j] = v;
                }
            }
        }
    }
    pred = predecessors;
    return distances[sink];
}

// get the actual order of the shortest path
export function getShortestPath(g: graph, origin: number, sink: number) {
    const reversePath = [];
    const path = [];
    let current = sink;
    reversePath.push(sink)
    while (current != origin){
        const prev = pred[current];
        reversePath.push(prev);
        current = prev;
    }
    for (let i = reversePath.length - 1; i >= 0; i --){
        path.push(reversePath[i]);
    } 
    return path;
}

// returns the nodes of the shortest path by letters
export function getSPALpha(path: number[]){
    const alphaPath = [];
    for (let i = 0; i < path.length; i++){
        alphaPath.push(alphabet[path[i]]);
    }
    return alphaPath;
}

