export function getAllShortestPaths(g: graph, origin: number){
    const paths = new Set();
    for (let i = 0; i < g.nVertices; i++){
        if (i == origin){
            continue;
        }
        dijkstra(g, origin, i);
        const curr = getShortestPath(g, origin, i);
        paths.add(curr);
    }
    
    return paths;
}

export function createAdj(g: graph){
    const adj = [];
    for (let i = 0; i < g.nVertices; i ++){
        adj.push([]);
    }
    for (let i = 0; i < g.nEdges; i ++){
        const v1 = g.edges[i].v1;
        const v2 = g.edges[i].v2;
        adj[v1].push(v2);
        adj[v2].push(v1);
    }
    return adj;
}

