// formats input edges in path order and outputs nicely formated string
export function formatPath(g: graph, edgeNumbers: number[], origin: number, sink: number): string{
    let formated = "";
    const numNodes = [];
    const alphaNodes = [];

    numNodes.push(origin);
    let current = origin;
    
    while(current != sink){
        for (let i = 0; i < edgeNumbers.length; i++){
            if(edgeNumbers[i] < g.nEdges){
                const one = g.edges[edgeNumbers[i]].v1;
                const two = g.edges[edgeNumbers[i]].v2;
                if (current == one){
                    numNodes.push(two);
                    current = two;
                    edgeNumbers[i] = g.nEdges;
                    break;
                } else if (current == two){
                    numNodes.push(one);
                    current = one;
                    edgeNumbers[i] = g.nEdges;
                    break;
                }
            }
        }
        
    }
    for (let i = 0; i < numNodes.length; i++){
        alphaNodes.push(alphabet[numNodes[i]]);
    }

    for (let i = 0; i < alphaNodes.length -1 ; i++){
        formated += alphaNodes[i];
        formated += " - "
    }
    formated += alphaNodes[alphaNodes.length - 1];
    
    return formated;
    
}



